import React, { Component } from 'react';
import './Masthead.css';


export default class Element extends Component {

    componentDidMount() {
        require('../../js/external/twitter.js')();
    }

    render() {

	 return (
            <div className="Masthead">
                <div className="Masthead__item">
                    <span className="Masthead__label white">POINGS LIS</span>
                    <span className="Masthead__details">Un webdocumentaire ralis par les tudiants de l'cole de journalisme de Grenoble (EJDG)<a href="//twitter.com/EDJGrenoble" className="twitter-follow-button" data-show-count="false" data-lang="fr" data-dnt="true">Suivre</a></span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Sous la supervision de </span>
                    <span className="Masthead__details">Cdric Cousseau et Thibault Lefvre</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Rdacteurs en chef</span>
                    <span className="Masthead__details">Martin Cauwel et Charlotte Machado</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Journalistes</span>
                    <span className="Masthead__details">Luc Chemla, Lo Corcos, Flix Gabory, Justine Guitton-Boussion, Alexandre Lepre, Alexandre Loc'h, Venantia Petillault et Xavier Ponroy</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Graphisme</span>
                    <span className="Masthead__details">Louise Klethi, Cline Legay et lise Martin</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Codage</span>
                    <span className="Masthead__details">Julien Rolland</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Remerciements</span>
                    <span className="Masthead__details">Rnald Garrido, Kayser, lodie et l'ensemble des boxeurs du CS Savinois ainsi que Robbie Davies Jr et son quipe</span>
                </div>
            </div>
        );
    }
}



